'use client';

import { useState } from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Bar,
  BarChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';

// Repository list
const repositories = [
  'Prebid.js',
  'Prebid Server',
  'Prebid Server Java',
  'Prebid Mobile iOS',
  'Prebid Mobile Android',
  'Prebid Docs',
];

// Dummy data for the chart
const chartData = [
  { month: 'Jan', value: 186 },
  { month: 'Feb', value: 305 },
  { month: 'Mar', value: 237 },
  { month: 'Apr', value: 73 },
  { month: 'May', value: 209 },
  { month: 'Jun', value: 214 },
];

// Stat card data
const stats = [
  { label: 'Open PRs', value: '234', trend: '+12.5%' },
  { label: 'Merged PRs', value: '1,429', trend: '+18.2%' },
  { label: 'Open Issues', value: '89', trend: '-3.1%' },
  { label: 'Closed Issues', value: '892', trend: '+7.8%' },
];

export default function Home() {
  const [selectedRepo, setSelectedRepo] = useState(repositories[0]);
  const [activeMetric, setActiveMetric] = useState(0);

  return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      <Card className="w-full">
        <CardHeader className="flex flex-col gap-2 space-y-0 pb-4">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-2">
            <CardTitle className="text-2xl font-bold">
              {selectedRepo}
            </CardTitle>
            <CardDescription>
              Repository activity and metrics
            </CardDescription>
          </div>
        </CardHeader>
        
        <CardContent className="pb-4">
          {/* Stat Cards */}
          <div className="grid grid-cols-2 gap-4 sm:grid-cols-4 mb-6">
            {stats.map((stat, index) => (
              <button
                key={stat.label}
                onClick={() => setActiveMetric(index)}
                className={`flex flex-col gap-1 rounded-lg border p-4 text-left transition-all hover:bg-accent ${
                  activeMetric === index 
                    ? 'border-primary bg-accent' 
                    : 'border-border'
                }`}
              >
                <span className="text-2xl font-bold">
                  {stat.value}
                </span>
                <span className="text-xs text-muted-foreground">
                  {stat.label}
                </span>
                <span className={`text-xs font-medium ${
                  stat.trend.startsWith('+') 
                    ? 'text-green-600 dark:text-green-400' 
                    : 'text-red-600 dark:text-red-400'
                }`}>
                  {stat.trend} from last month
                </span>
              </button>
            ))}
          </div>

          {/* Chart */}
          <div className="h-[300px] w-full">
            <ResponsiveContainer width="100%" height="100%">
              <BarChart data={chartData}>
                <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                <XAxis 
                  dataKey="month"
                  className="text-xs"
                />
                <YAxis 
                  className="text-xs"
                />
                <Tooltip 
                  contentStyle={{
                    backgroundColor: 'hsl(var(--background))',
                    border: '1px solid hsl(var(--border))',
                    borderRadius: '6px',
                  }}
                />
                <Bar 
                  dataKey="value" 
                  fill="hsl(var(--primary))"
                  radius={[4, 4, 0, 0]}
                />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </CardContent>
      </Card>

      {/* Repository Selector */}
      <div className="mt-6">
        <div className="flex flex-wrap gap-2">
          {repositories.map((repo) => (
            <button
              key={repo}
              onClick={() => setSelectedRepo(repo)}
              className={`px-4 py-2 rounded-lg border transition-all ${
                selectedRepo === repo
                  ? 'bg-primary text-primary-foreground border-primary'
                  : 'bg-background border-border hover:bg-accent'
              }`}
            >
              {repo}
            </button>
          ))}
        </div>
      </div>
    </div>
  );
}